/* Existing c-r-a CSS */
.App {
  text-align: center;
}

.App-header {
  background-color: #222;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

/* My additional styling - needs to be properly responsive */
.survey-body {
  max-width: 960px;
  margin: 0 auto;
}

/* List formatting */
ol.scale-list {
  margin: 0;
  padding: 0;
  counter-reset: list-c;
}

ol.scale-list>li {
  display: inline-block;
  counter-increment: list-c;
  padding: 0 25px;
  position: relative;
}

ol.scale-list>li:before {
  content: counter(list-c) '.';
  display: inline-block;
  text-align: center;
  position: absolute;
  left: -2px;
}

